import { ENDPOINTS } from "../constants/endpoints";
import requestService from "./request";


export class GeneralService {
    static async getWorkSummary(startDate = null,endDate = null){
        const body ={
            startDate : startDate,
            endDate: endDate
        }
        if(!startDate || !endDate) return new Date().formatUTC()
        const res = await requestService.send({
            method:"get",
            path:ENDPOINTS.RES_WORK_SUMMARY,
            data:body
        })
        return res
    }
}