// @ts-nocheck
import axios from 'axios'
import { HOST } from './../constants/url'
import 'antd/dist/antd.css'
import { notification } from 'antd'
import {
  getQueryString
} from '../helper/common'

function openNotification(placement, message) {
  notification.error({
    message: `${message}`,
    placement,
  });
};

function send({
  method = 'get', path, data = null, query = null, headers = {}, newUrl
}) {
  return new Promise((resolve) => {
    let url = HOST + `${path}${getQueryString(query)}`
    if (newUrl) {
      url = `${newUrl}${getQueryString(query)}`
    }
    let token = window.localStorage.getItem('accessToken')
    
    if (token) {
      const newToken = token.replace(/"/g, "");
      headers.Authorization =`Bearer ${newToken}`
    }
    axios({
      method, url, data, headers,
    })
      .then((result) => {
        const data = result.data
        return resolve(data)
      })
      .catch((error) => {
        const {response ={}} = error
        const result = response.data ? response.data :null
        
        if (!result) {
          openNotification('topRight', "Internal Server Error")
        }
        else {
          const { statusCode, message: data } = result
          if(statusCode === 505){
            openNotification('topRight', "Unauthorized was wrong")
            window.localStorage.clear()
            window.location.href = '/login'
          }
         else if (statusCode === 401 && data === 'Expired token received for JSON Web Token validation') {
            window.localStorage.clear()
            window.location.href = '/'
          }
          else if (
            (statusCode === 401 && data === 'Unauthorized') || (statusCode === 403 && data === 'InvalidToken')) {
              window.localStorage.clear()
              window.location.href = '/'
          } else if(statusCode) {
            openNotification('topRight', data || "Internal Server Error")
          }
          else {
            return resolve(result.data)
          }
        }
      })
  })
}

export default {
  send,
}
