import { useEffect, useState } from "react";
// import { Row, Col, Card, CardHeader, CardTitle, CardBody, Media } from 'reactstrap'
import { Row, Col } from "antd";
import Chart from "./Chart";
import "./General.css";
import "antd/dist/antd.css";
import { ReactComponent as IconBusiness } from "@assets/icons/IconBusiness.svg";
import { ReactComponent as IconThunder } from "@assets/icons/IconThunder.svg";
import { ReactComponent as IconCup } from "@assets/icons/IconCup.svg";
import { ReactComponent as IconDollar } from "@assets/icons/IconDollar.svg";
import CardSummary from "@src/views/ui-elements/cards/summary";
import { GeneralService } from "../../../services/general";
import TaskProgressBar from "@components/task-progress-bar";
import {Card} from 'reactstrap'

const General = () => {
  const [dataSummary, setDataSummary] = useState({
    Approval: 0,
    Alert: 0,
    Finished: 0,
    ApprovalMoney: 0,
  });

  const resData = {
    approve: {
      icon: <IconBusiness />,
      statsTitle: "Bất Động Sản được duyệt",
      startDate: "15/10",
      formatData: (data) => dataSummary.Approval,
    },
    warning: {
      icon: <IconThunder />,
      bgColor: "#F16856",
      statsTitle: "Bất động sản cảnh báo",
      startDate: "15/10",
      formatData: (data) => dataSummary.Alert,
    },
    target: {
      icon: <IconCup />,
      bgColor: "#36CBCB",
      statsTitle: "Hoàn Thành Chỉ Tiêu",
      startDate: "15/10",
      formatData: (data) => dataSummary.Finished + "%",
    },
    moneyApprove: {
      icon: <IconDollar />,
      bgColor: "#55D5F1",
      statsTitle: "Số tiền được duyệt",
      startDate: "15/10",
      formatData: (data) => dataSummary.ApprovalMoney + "tr",
    },
  };

  const taskData = {
    'done': 50,
    'progress': 30,
    'cancel': 20,
  }

  useEffect(() => {
    const fetchData = async () => {
      const startDate = "12/12/2001",
        endDate = "12/10/2022";
      const getWorkSummary = await GeneralService.getWorkSummary(
        startDate,
        endDate
      );
      if (getWorkSummary) setDataSummary(getWorkSummary);
    };
    fetchData();
  }, []);

  return (
    <>
      <Row className="mb-2" gutter={16} style={{ rowGap: "25px" }}>
        <Col lg={6} md={12} sm={12} xs={24}>
          <CardSummary {...resData.approve} />
        </Col>
        <Col lg={6} md={12} sm={12} xs={24}>
          <CardSummary {...resData.warning} />
        </Col>
        <Col lg={6} md={12} sm={12} xs={24}>
          <CardSummary {...resData.target} />
        </Col>
        <Col lg={6} md={12} sm={12} xs={24}>
          <CardSummary {...resData.moneyApprove} />
        </Col>
      </Row>

      <Row gutter={16} className="row-chart">
        <Col lg={12} md={12} sm={24} xs={24} >
          <div className="dashboardMissionTracking">
            <Card>
              <Chart style={{borderRadius: "25px !important"}}/>
            </Card>
          </div>
        </Col>
        <Col lg={12} md={12} sm={24} xs={24}>
          <div className="dashboardMissionTracking">
            <TaskProgressBar tasks={taskData}/>
          </div>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col lg={24} md={24} sm={24} xs={24}>
          <div className="dashboardEstateList"></div>
        </Col>
      </Row>
      <Row gutter={16}>
        <Col lg={24} md={24} sm={24} xs={24}>
          <div className="dashboardEstateList"></div>
        </Col>
      </Row>
    </>
  );
};

export default General;
