import React from 'react';
import { useState, useEffect, memo } from "react";
import './Chart.css'
import { injectIntl } from 'react-intl';
import { AreaChart, Area, YAxis, XAxis, CartesianGrid, Tooltip, CustomTooltip, Legend, ResponsiveContainer } from 'recharts';
import { Row, Col } from 'antd';
import 'antd/dist/antd.css'

const rangeData = [
  {
    'day': "",
    'BĐS': 0
  },
  {
    'day': "Thứ 2",
    'BĐS': 20
  },
  {
    'day': "Thứ 3",
    'BĐS': 12
  },
  {
    'day': "Thứ 4",
    'BĐS': 16
  },
  {
    'day': "Thứ 5",
    'BĐS': 14
  },
  {
    'day': "Thứ 6",
    'BĐS': 24
  },
  {
    'day': "Thứ 7",
    'BĐS': 15
  },
  {
    'day': "Chủ Nhật",
    'BĐS': 23
  },
  {
    'day': "",
    'BĐS': 0
  }
]

const Chart = ({ intl }) => {

  const dot = {
    stroke: '#544CA6',
    strokeWidth: 1,
    r: 3,
    strokeDasharray: ''
  }
  const margin = {
    top: 20,
    right: -25,
    bottom: 20,
    left: -25,
  }
  let tooltip
  const formatXAxis = (value) => {
    return value
  }
  const CustomTooltip = ({ active, payload, label }) => {

    if (active && payload) {
      if (payload[0].value === 0) {
        return null
      } else {
        return <div className='wrapperHover'>
          <div className='value'>{payload[0].value} {payload[0].name}</div>
          <div className='label'>{label}</div>
        </div>
      }
    }
    return null
  }
  return (
    <div className='wrapperChartLine'>
      <Row className='styleRow'>
        <Col lg={12} md={12} sm={12} xs={24} >
          <div className='imageChart'></div>
        </Col>
        <Col lg={12} md={12} sm={12} xs={24}  >
          <h2>{intl.formatMessage({ id: 'text-h1' })}</h2>
          <p className='textNotifi'>{intl.formatMessage({ id: 'text-notifi' })}
            <span className='textRed'> 4</span> {intl.formatMessage({ id: 'text-mission' })}
            <span className='textRed'> 67%</span>. {intl.formatMessage({ id: 'text-work-progress' })}
            <span className='textBlue'> {intl.formatMessage({ id: 'text-normal' })}</span>.
          </p>
        </Col>
      </Row>
      <div className='wrapperLine'>
        <p className='textHeader'>{intl.formatMessage({ id: 'text-quanlity' })}</p>
        <div style={{ height: '147px', width: '100%' }}>
          <ResponsiveContainer>
            <AreaChart
              data={rangeData}
              margin={margin}
            >
              <defs>
                <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="5%" stopColor="#8884d8" stopOpacity={0.8} />
                  <stop offset="95%" stopColor="#8884d8" stopOpacity={0} />
                </linearGradient>
              </defs>
              <XAxis dataKey='day' tickFormatter={formatXAxis} />
              <Area dataKey="BĐS" stroke="#8884d8" fill="url(#colorUv)" fillOpacity={0.15}
                onMouseOver={() => tooltip = "BĐS"}
                dot={dot} activeDot={{ r: 5 }} ></Area>
              <Tooltip name='BĐS' content={<CustomTooltip />} />
            </AreaChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  )
}
export default injectIntl(memo(Chart))
// export default Chart