import { useEffect, useState } from "react";
import StatsSummary from "@components/widgets/stats/StatsSummary";
import { ReactComponent as IconArrowDown } from "@assets/icons/IconArrowDown.svg";

const CardSummary = (props) => {
  const { icon, bgColor, statsTitle, startDate, formatData } = props;
  const renderContentFooter = (
    <>
      Từ {startDate} đến Hôm Nay <IconArrowDown />
    </>
  );

  const [data, setData] = useState(0);
  
  useEffect(() => {
    // call api
  }, []);

  return (
    <StatsSummary
      icon={icon}
      bgColor={bgColor}
      stats={formatData(data)}
      statsTitle={statsTitle}
      footerContent={renderContentFooter}
    />
  );
};

export default CardSummary;
