// ** Third Party Components
import PropTypes from "prop-types";
import {ReactComponent as IconMore} from '@assets/icons/IconMore.svg'

import {
  SCContainer,
  SCFooter,
  SCHeader,
  SCStats,
  SCStatsTitle,
} from "./StatsSummary.style";

const StatsSummary = ({
  icon,
  bgColor,
  stats,
  statsTitle,
  startDate,
  className,
  footerContent,
  ...rest
}) => {
  return (
    <SCContainer {...rest} bgColor={bgColor} className={className}>
      <SCHeader>{icon} <IconMore type="w-click"/></SCHeader>
      <SCStats>{stats}</SCStats>
      <SCStatsTitle>{statsTitle}</SCStatsTitle>
      <SCFooter type="w-click"> 
        {footerContent}
      </SCFooter>
    </SCContainer>
  );
};

export default StatsSummary;

// ** PropTypes
StatsSummary.propTypes = {
  icon: PropTypes.element.isRequired,
  bgColor: PropTypes.string,
  stats: PropTypes.string.isRequired,
  statsTitle: PropTypes.string.isRequired,
  startDate: PropTypes.string.isRequired,
  footerContent: PropTypes.element,
  className: PropTypes.string,
};
