import styled from "styled-components";

const COLOR_MAIN = "#544CA6";
export const SCContainer = styled((props) => <div {...props} />)`
  border-radius: 22px;
  padding: 20px 25px;
  min-height: 200px;
  max-height:200px;
  display: flex;
  width:100%;
  flex-direction: column;
  justify-content: space-between;
  color: #fff;
  background-color: ${(props) => props.bgColor || COLOR_MAIN};
  [type=w-click]{
    cursor:pointer;
  }
  h1,
  h2,
  p {
    color: #fff;
    overflow:hidden;
    text-overflow:ellipsis;
    -webkit-line-camp:2;
    -webkit-box-orient:vertical;
    display:-webkit-box;
  }
`;

export const SCHeader = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
`;

export const SCStats = styled.h1`
  font-size: 60px !important;
  font-weight: 800;
  margin:0 !important;
`;
export const SCStatsTitle = styled.p`
  font-size: 14px;
  height: 30%;
  line-height: 18px;
  font-weight: 400;
  margin-bottom: 5px !important;
  text-transform: capitalize;
`;
export const SCFooter = styled.div`
  display: flex;
  font-size: 10px;
  font-weight: bold;
  height: 10%;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
`;
