import { Progress } from 'reactstrap';
import './ProgressMultipleStacked.css';

const ProgressMultipleStacked = (props) => {
  return (
    <Progress multi>
      <Progress bar className='taskDone' value={props.tasks.done}/>
      <Progress bar className='taskOver' value={props.tasks.cancel}/>
      <Progress bar className='taskProgress' value={props.tasks.progress}/>
    </Progress>
  )
}
export default ProgressMultipleStacked
