import { Card, CardHeader, CardTitle, CardBody } from "reactstrap";
import ProgressMultipleStacked from "@src/views/components/progress/ProgressMultipleStacked";
import { Row, Col } from "antd";
import { ReactComponent as IconLegendOver } from "@assets/icons/IconLegendOver.svg";
import { ReactComponent as IconLegendProgress } from "@assets/icons/IconLegendProgress.svg";
import { ReactComponent as IconLegendDone } from "@assets/icons/IconLegendDone.svg";
import { ReactComponent as IconTaskProgressCup } from "@assets/icons/IconTaskProgressCup.svg";
import "../../scss/base/components/task-progress.scss";
import BackgroundTask from "@src/assets/images/backgrounds/progress-bg.png";

const TaskProgressBar = (props) => {
  return (
    <Card style={{borderRadius:'24px'}}>
      <CardHeader>
        <CardTitle tag="h2">Tiến độ</CardTitle>
      </CardHeader>
      <CardBody>
        <Row gutter={10} wrap={true} align="middle" justify="space-between">
          <Col span={20} sm={20} xs={24}>
            <ProgressMultipleStacked tasks={props.tasks} />
            <Row>
              <Col lg={6} md={8} sm={8} xs={24}>
                <IconLegendOver /> <span className="legendText">Quá hạn</span>
              </Col>
              <Col lg={6} md={8} sm={8} xs={24}>
                <IconLegendDone />{" "}
                <span className="legendText">Đã hoàn thành</span>
              </Col>
              <Col lg={6} md={8} sm={8} xs={24}>
                <IconLegendProgress />{" "}
                <span className="legendText">Chưa hoàn thành</span>
              </Col>
            </Row>
          </Col>
          <Col span={4} sm={4} xs={0} style={{textAlign:'right'}}>
            <IconTaskProgressCup />
          </Col>
        </Row>
        <div className="mt-2">
          <h2 className="mb-1">Theo Dõi Nhiệm Vụ</h2>
          <p>Chưa có nhiệm vụ mới được cập nhật.</p>
          <img src={BackgroundTask} className="card-img" />
        </div>
      </CardBody>
    </Card>
  );
};
export default TaskProgressBar;
